* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

.active {
    background-color: #111;
}

.container::after {
    content: "";
    clear: both;
    display: block;
}

.main {
    width: 100%;
    margin-top: 38px;
}

.visited {
    background-color: #49ff3317;
}

button {
    background-color: cornflowerblue;
    color: rgb(250, 250, 250);
    border: none;
    margin: 2px;
    padding: 3px 10px;
    text-align: center;
    text-decoration: none;
}

.main > ol {
    padding: 5px;
}

.main > ol > li {
    color: rgb(0, 134, 139);
    margin-bottom: 4px;
    padding: 5px;
    list-style-position: inside;
    border: 1px solid rgb(211,211,211);
    border-radius: 5px;
    box-shadow:         0 2px rgb(240,240,240);
    position: relative;
}

li > input {
    display: block;
    width: 100%;
    border-top: none;
    border-right: none;
    border-bottom: 1px dashed rgb(88,88,88);
    border-left: none;
}

summary {
    padding: 5px;
}

summary:hover {
    cursor: pointer;
}

details > p {
    color: green;
}

@media only screen and (max-width: 768px) {
    .main {
        width: 100%;
    }
    .main > ol {
        margin-top: 0;
    }
}
.header {
    padding: 10px 0 10px 0;
    background-color: #333;
    position: fixed;
    overflow: hidden;
    top: 0;
    width: 100%;
    z-index: 1000;
}

.header > .nav-item {
    padding: 10px 10px 10px 5px;
    color: white;
    text-align: center;
    text-decoration: none;

}

.header > .nav-item:hover:not(span) {
    background-color: #111;
}
#wordsNavList {
    list-style-type: none;
    text-align: center;
}

#wordsNavList > li > a {
    display: block;
    text-decoration: none;
    color: #000;
}

#wordsNavList > li > span.score {
    position: absolute;
    bottom: 4px;
    right: 5px;
}
body {
    margin: 0;
    padding: 0;
    font-family: sans-serif;
}

